body{
    background-color: rgb(198, 228, 255);
}

.form-check-input:checked {
    background-color: #9dff26;
    border-color: #9dff26;
}

.form-check-input:focus {
    outline: 0;
    box-shadow: 0 0 0 0.25rem rgba(93, 253, 13, 0.25);
}

.btn-lgreen {
    color: #fff;
    background-color: #31d100;
    border-color: #31d100;
}
.btn-lgreen:hover {
    color: #fff;
    background-color: #28aa00;
    border-color: #28aa00;
}
.btn-lgreen:focus {
    color: #fff;
    background-color: #28aa00;
    border-color: #28aa00;
    box-shadow: 0 0 0 0.25rem rgba(40, 170, 0, 0.5);
}

.spinner-border {
    width: 1.5rem;
    height: 1.5rem;
}